.content__img {
  width: 800px;
}

.descriptionBlock {
  padding: 10px;
}

.image {
  width: 100px;
}

.image img {
  border-radius: 50%;
  width: 100%;
}

.aboutUser {
  margin-bottom: 5px;
}

.findJob {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.circle {
  margin-left: 15px;
}

.circleGreen, .circleRed {
  width: 10px;
  height: 10px;
  border-radius: 50%;
}

.circleGreen {
  background-color: green;
}

.circleRed {
  background-color: red;
}